import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { supabase } from "../../helper/supabaseClient";
import Header from "../../components/global/header/Header";
import classes from "./ProductPage.module.css";
import starImg from "../../img/searchPage/star.svg";
import imgFavorite from "../../img/favorite.svg";
import ProductTable from "./productPage components/ProductTable";

interface Product {
  id: string;
  cpuName: string;
  socket: string;
  price: string;
  year: number;
  coresNumber: number;
  threads: number;
  cacheL2: number;
  cacheL3: number;
  frequency: number;
  overclockedFrequency: string;
  img: string;
  DDR: string;
  maxRam: string;
  multiplier: boolean;
  ramFrequency: number;
  TDP: number;
  maxHeat: number;
  integratedGPU: boolean;
  ramChannels: number;
}

const ProductPage: React.FC = () => {
  const { productId }: { productId?: string } = useParams();
  const parsedProductId = parseInt(productId || "", 10);
  const [product, setProduct] = useState<Product | null>(null);
  const [additionalButtonsState, setAdditionalButtonsState] = useState(1);

  const fetchData = async (parsedProductId: number) => {
    try {
      const { data } = await supabase
        .from("cpu")
        .select("*")
        .eq("id", `${parsedProductId}`);
      if (data) {
        setProduct(data[0]);
      }
    } catch (error) {
      console.error("Error fetching supabase data", error);
    }
  };

  useEffect(() => {
    const updateHistory = (id: number) => {
      const history = JSON.parse(localStorage.getItem("history") || "[]");
      if (!history.includes(id)) {
        history.push(id);
        localStorage.setItem("history", JSON.stringify(history));
      }
    };

    updateHistory(parsedProductId);
    fetchData(parsedProductId);
  }, [parsedProductId]);

  useEffect(() => {
    let num = 15;
    console.log((num = 20));
    console.log(num);
  }, []);

  if (!product) {
    return (
      <div>
        <Header />
        <div style={{ background: "F6F6F6", height: "800px" }} />
      </div>
    );
  }

  return (
    <div>
      <Header />
      <div className={classes.product_background}>
        <div className={classes.product_container}>
          <h1> {product.cpuName}</h1>
          <div className={classes.product_card}>
            <div className={classes.product_card_main}>
              <img
                src={product.img}
                alt="product img"
                className={classes.product_img}
              />
              <div className={classes.product_characteristics}>
                <h3>
                  {product.socket}, {product.coresNumber} x {product.frequency}{" "}
                  , L2 - {product.cacheL2} , L3 - {product.cacheL3} ,{" "}
                  {product.ramChannels} x {product.DDR}-{product.ramFrequency}{" "}
                  , TDP {product.TDP} 
                </h3>
                <div className={classes.stars}>
                  <img src={starImg} alt="rating img" />
                  <img src={starImg} alt="rating img" />
                  <img src={starImg} alt="rating img" />
                  <img src={starImg} alt="rating img" />
                  <img src={starImg} alt="rating img" />
                  <h4>10</h4>
                </div>
                <div className={classes.product_section_price}>
                  <div className={classes.product_section_price_cost}>
                    <h2>{product.price}</h2>
                  </div>
                  <button className={classes.fav_button}>
                    <img src={imgFavorite} alt="imgFavorite" />
                  </button>
                  <button className={classes.product_section_price_buy}>
                    <h2></h2>
                  </button>
                </div>
                <div className={classes.product_avails}>
                  <div className={classes.availability}>
                    <h2> </h2>
                    <h3> ( )</h3>
                  </div>
                  <div className={classes.availability}>
                    <h2>  </h2>
                    <h3>   </h3>
                  </div>
                </div>
                <div className={classes.mockup_buttons_row}>
                  <button
                    className={
                      additionalButtonsState === 1 ? classes.active_button : ""
                    }
                    onClick={() => {
                      setAdditionalButtonsState(1);
                    }}
                  >
                    <h2>Mockup-</h2>
                  </button>
                  <button
                    className={
                      additionalButtonsState === 2 ? classes.active_button : ""
                    }
                    onClick={() => {
                      setAdditionalButtonsState(2);
                    }}
                  >
                    <h2></h2>
                  </button>
                  <button
                    className={
                      additionalButtonsState === 3 ? classes.active_button : ""
                    }
                    onClick={() => {
                      setAdditionalButtonsState(3);
                    }}
                  >
                    <h2></h2>
                  </button>
                  <button
                    className={
                      additionalButtonsState === 4 ? classes.active_button : ""
                    }
                    onClick={() => {
                      setAdditionalButtonsState(4);
                    }}
                  >
                    <h2></h2>
                  </button>
                </div>
              </div>
            </div>
            <div className={classes.product_info}>
              <h2>  {product.cpuName}</h2>
              <ProductTable
                title=" "
                parameters={[
                  "  / ",
                  "-",
                ]}
                values={["12 ", ""]}
              />
              <ProductTable
                title=" "
                parameters={["", "", " "]}
                values={[
                  `${product.cpuName}`,
                  `${product.socket}`,
                  `${product.year}`,
                ]}
              />
              <ProductTable
                title="  "
                parameters={[
                  "  ",
                  "  ",
                  "  ",
                  "  L2",
                  "  L3",
                ]}
                values={[
                  `${product.coresNumber}`,
                  `${product.coresNumber}`,
                  `${product.threads}`,
                  `${product.cacheL2} `,
                  `${product.cacheL3} `,
                ]}
              />
              <ProductTable
                title="   "
                parameters={[
                  "  ",
                  "    ",
                  " ",
                ]}
                values={[
                  `${product.frequency} `,
                  `${product.overclockedFrequency} `,
                  `${product.multiplier === true ? "" : ""}`,
                ]}
              />
              <ProductTable
                title="  "
                parameters={[
                  " ",
                  "   ",
                  " ",
                  "  ",
                ]}
                values={[
                  `${product.DDR}`,
                  `${product.maxRam} `,
                  `${product.ramChannels}`,
                  `${product.ramFrequency} `,
                ]}
              />
              <ProductTable
                title=" "
                parameters={[
                  "(TDP)",
                  " ",
                  "  ",
                ]}
                values={[
                  `${product.TDP} `,
                  `${product.TDP} `,
                  `${product.maxHeat} C`,
                ]}
              />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ProductPage;
